import getField from './fieldMapping'
import StaticField from './StaticField'

export default {
  name: 'GenericInput',
  functional: true,
  props: {
    name: {},
    type: {
      type: String, // editor
      default: 'string'
    },
    value: {},  // 
    static: {
      type: Boolean,
      default: false
    }, //
    options: Object, // 
    multiple: {
      type: Boolean,
      default: false
    },
    format: {}, //
    placeholder: {},
    info: {},
    required: {
      type: Boolean,
      default: false
    },
    staticSuffix: {
      type: String,
      default: '_static'
    }
  },

  render (h, ctx) {
    let self = ctx.props
    let static_name = `${self.name}${self.staticSuffix}`
    let InputClass = getField(self.type)
    let input = new InputClass(ctx.parent, ctx.props)
    if (self.static) {
      //name_staticsetStaticValue
      let v = self.value[`${self.name}${self.staticSuffix}`]
      if (v === undefined || v === null)
        input.setStaticValue(self.value[self.name])

      return h(StaticField, {props: ctx.props})
    }

    return input.render(h, ctx)
  }
}



